.gooey-container{
  position: relative;
  height: 200px;
}
.gooey{
  position: absolute;
  top: 50%;
  left: 50%;
  width: 142px;
  height: 40px;
  margin: -20px 0 0 -71px;
  background: white;
  filter: contrast(20);
  z-index: 100;
}
.gooey .dot{
position: absolute;
width: 14px;
height: 14px;
top: 12px;
left: 15px;
filter: blur(4px);
background: #000;
border-radius: 50%;
transform: translateX(0);
animation: dot 2.8s infinite}
.gooey .dots{
transform: translateX(0);
margin-top: 12px;
margin-left: 31px;
animation: dots 2.8s infinite;
}
.gooey .dots span{
  display: block;
  float: left;
  width: 14px;
  height: 14px;
  margin-left: 16px;
  filter: blur(4px);
  background: #000;
  border-radius: 50%}


@keyframes dot{
  50%{
    transform: translateX(96px)}}
@keyframes dots{
  50%{
    transform: translateX(-31px)}}